

import Vue from 'vue'
import App from './App'
import router from './router'
import FastClick from 'fastclick';
import store from './store/store';
import './utils/filter';
import VueScroller from 'vue-scroller';
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'


Vue.use(VueScroller);
Vue.use(ToastPlugin);
Vue.use(LoadingPlugin);
Vue.use(AlertPlugin);

const options = {
duration: '0.3',              //0.3
firstEntryDisable: false,     //truefalse
firstEntryDuration: '.6',     //.6
forwardAnim: 'pulse',   //fadeInRight
backAnim: 'fadeInLeft',       //fedeInLeft
sameDepthDisable: false,      //urlfalse
tabs: [{
name: 'Home'
}, {
name: 'punchCard'
}],                       //[]namename,apptabtab[1]tab[0]backAnimtab[1]tab[2]forwardAnim
tabsDisable: false,           //truetabsfalse
disable: false,               //falsetrue
};

Vue.config.productionTip = false;
FastClick.attach(document.body);

if (window.location.protocol === 'file:' || window.location.port === '3000') {
let cordovaScript = document.createElement('script');
cordovaScript.setAttribute('type', 'text/javascript');
cordovaScript.setAttribute('src', 'cordova.js');
document.body.appendChild(cordovaScript)
}

/* eslint-disable no-new */
new Vue({
el: ' #app',
router,
template: '<App/>',
components: {App}
});



