Vue.filter('switchType', function (param) {
  switch (param) {
    case 'vacationInfo':
      return '';
      break;
    case 'applyHoliday':
      return '';
      break;
    case 'applyOvertime':
      return '';
      break;
    case 'applySupplySign':
      return '';
      break;
    default:
      return param;
      break;
  }
});

